
// Tabs

.tab-container {
  display: block;
}

.nav.nav-tabs {
  padding: 0 ($spacer / 1.5);

  .nav-item {
    padding: 0 ($spacer / 6);

    .nav-link {
      font-size: $font-size-sm;
      text-transform: uppercase;
      margin: 2px 0 0 0;
      color: $gray-700;
      background: transparent;
      transition: $transition-base;
      cursor: pointer;

      &:hover {
        color: $black;
        background: transparent;
      }

      &.active {
        margin-top: 0;
        color: $primary;
        background: transparent;
        border-top: $primary solid 3px;

        &:hover {
          color: $primary;
        }
      }
    }

  }
}

@include media-breakpoint-down(lg) {

  .tabs-responsive {
    .nav.nav-tabs {
      flex-wrap: wrap;
      display: flex;

      .nav-item {
        text-align: center;
        width: 50%;
      }
    }
  }
}

@include media-breakpoint-down(md) {

  .tabs-responsive {
    .nav.nav-tabs {
      flex-wrap: wrap;
      display: flex;

      .nav-item {
        text-align: center;
        width: 100%;
      }
    }
  }
}

.nav-justified {
  .nav-link {
    & > span {
      width: 100%;
    }
  }
}

// Line tabs

.nav.nav-line {
  .nav-item {
    .nav-link {
      color: $gray-700;
      position: relative;
      transition: $transition-base;
      padding: 0 $spacer $spacer;
      background: transparent;

      .divider {
        width: 34px;
        @include border-radius($border-radius-lg);
        height: 3px;
        background: $primary;
        visibility: hidden;
        opacity: 0;
        transform: translateY(-10px);
        transition: $transition-base;
        position: absolute;
        left: $spacer;
        bottom: 0;
      }

      &:hover {
        color: $black;
        background: transparent;
      }

      &.active {
        color: $primary;
        background: transparent;

        &:hover {
          color: $primary;
        }

        .divider {
          visibility: visible;
          opacity: 1;
          transform: translateY(0);
        }
      }
    }
  }
}

// Line tabs alternate

.nav-line-alt {
  .nav.nav-line {
    position: relative;
    z-index: 4;

    &::after {
      background: $gray-200;
      height: 3px;
      @include border-radius($border-radius-lg);
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      content: "";
      z-index: 5;
    }

    .nav-item + .nav-item {
      margin-left: ($spacer * 2);
    }

    .nav-item {
      .nav-link {
        padding: 0 0 $spacer;
        margin: 0;

        .divider {
          z-index: 8;
          left: 0;
          width: 100%;
        }
      }
    }
  }
}

// Header tabs

.header-tabs {
  & > .nav {
    display: none;
  }
}

// Animated

.tabs-animated {

  & > .nav-tabs {
    border: 0;

    .nav-item {
      .nav-link {
        border: 0 !important;
        position: relative;
        padding: ($layout-spacer / 4) ($layout-spacer / 2);
        margin: 0 ($layout-spacer / 4) 0 0;
        color: $body-color;
        text-transform: none;
        font-size: $font-size-base;
        cursor: pointer;

        &::before {
          transform: scale(0);
          opacity: 1;
          width: 100%;
          left: 0;
          bottom: -2px;
          content: "";
          position: absolute;
          display: block;
          @include border-radius($border-radius-lg);
          background: $primary;
          transition: all .2s;
          height: 4px;
        }

        &.active,
        &:hover {
          color: $primary;
          border: 0;

          &::before {
            transform: scale(1);
          }
        }

        &:last-child {
          margin-right: 0;
        }
      }
    }
  }

  &.tabs-bordered {
    & > .nav-tabs {
      border-bottom: $card-border-color solid 1px;
      padding: ($layout-spacer / 3) !important;

      .nav-item {
        .nav-link {
          margin-bottom: 0;
        }
      }
    }
  }
}

.tabs-animated-line {
  .nav-tabs {
    padding: 0 5px;

    .nav-link {
      cursor: pointer;

      span {
        width: 100%;
      }
    }
  }
}

.tabs-animated-shadow {
  & > .nav-tabs {
    position: relative;
    z-index: 9;

    .nav-item {
      .nav-link {
        padding: ($layout-spacer / 4) ($layout-spacer / 2);
        margin-bottom: ($layout-spacer / 4);
        cursor: pointer;
        border-radius: $border-radius-sm;

        span {
          position: relative;
          z-index: 5;
          display: inline-block;
          width: 100%;
        }

        &::before {
          height: 100%;
          top: 0;
          z-index: 4;
          bottom: auto;
          box-shadow: 0 16px 26px -10px rgba($primary, 0.56),
          0 4px 25px 0px rgba(0, 0, 0, 0.12),
          0 8px 10px -5px rgba($primary, 0.2);
          @include border-radius($border-radius-xs / 1.5);
          opacity: .5;
        }

        &.active,
        &:hover {
          color: $white !important;

          span {
            color: $white !important;
          }

          &::before {
            @include border-radius(inherit);
            opacity: 1;
          }
        }
      }

      .nav-item:last-child {
        .nav-link {
          margin-right: 0;
        }
      }
    }
  }
}

.tabs-rm-padding {
  & > .nav.nav-tabs {
    padding: 0 !important;

    .nav-item .nav-link {
      margin-bottom: 0 !important;
    }
  }
}

// Colors

@each $color, $value in $theme-colors {
  .nav-tabs-#{$color} {
    .nav.nav-tabs {
      .nav-item {
        .nav-link {

          &.active {
            color: $value;
            border-top: $value solid 3px;

            &:hover {
              color: $value;
            }
          }
        }
      }
    }

    .nav.nav-line {
      .nav-item {
        .nav-link {
          .divider {
            background: $value;
          }

          &.active,
          &.active:hover {
            color: $value;
          }
        }
      }
    }

    &.tabs-animated-shadow {
      & > .nav-tabs {
        padding: 0;

        .nav-item {
          .nav-link {
            color: $body-color;

            &::before {
              background: $value !important;
              box-shadow: 0 16px 26px -10px rgba($value, 0.56),
              0 4px 25px 0px rgba(0, 0, 0, 0.12),
              0 8px 10px -5px rgba($value, 0.2);
            }

            &.active,
            &:hover {
              color: color-yiq($value) !important;

              span {
                color: color-yiq($value) !important;
              }
            }
          }
        }
      }
    }

    &.tabs-animated-line {
      & > .nav-tabs {
        .nav-item {
          .nav-link {

            &.active,
            &:hover {
              color: $value;

              &::before {
                background: $value;
              }
            }
          }
        }
      }
    }
  }
}

