// Background

// Neutral

@each $color, $value in $theme-colors-neutral {
  @include bg-variant('.bg-neutral-#{$color}', $value);
}

:root {
  @each $color, $value in $theme-colors-neutral {
    --neutral-#{$color}: #{$value};
  }
}

// Brands

@each $color, $value in $brand-colors {
  @include bg-variant('.bg-brand-#{$color}', $value);
}

// Text brands

@each $color, $value in $brand-colors {
  @include text-emphasis-variant('.text-#{$color}', $value);
}

// Shadows

@each $color, $value in $theme-colors {
  .shadow-#{$color} {
    box-shadow: 0 0.313rem 0.8rem rgba($value, 0.5),
      0 0.126rem 0.225rem rgba($value, 0.3) !important;
  }

  .shadow-#{$color}-sm {
    box-shadow: 0 0.46875rem 2.1875rem rgba($value, 0.03),
      0 0.9375rem 1.40625rem rgba($value, 0.03),
      0 0.25rem 0.53125rem rgba($value, 0.05),
      0 0.125rem 0.1875rem rgba($value, 0.03) !important;
  }
}

